body {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
}

.stopwatch__hands {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
  position: relative;
}

.stopwatch__hands-min {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin-bottom: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
}

.stopwatch__hands-sec {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin-bottom: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
}

.stopwatch__hands-center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__hands-sec {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__hands-min {
  animation-duration: 600s;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}
